import ttNavBar from '@/components/tt-nav-bar/tt-nav-bar.vue'
export default {
	components:{ttNavBar},
	data() {
		return {
			imageList:[
				'../../static/home/2.jpg'
			],
			backgroundColor:'transparent',
			title: '',
			url: '',
			teacher: {
				avatar: '',
				nickname: '',
				phone: '',
				addr: '',
				lat:'',
				lon:'',
				signature: '',
				experienceTime: '',
				job: '',
				school: '',
				profession: '',
				course: '',
				photo: '',
				experience: '',
				intro: '',
			},
		}
	},
	mounted(){
		this.getData()
	},
	onLoad: function (option) {




		this.url = `${this.GLOBAL.baseURL}/m/user_detail/${option.id}`

	},
	methods: {
		previewImage(e) {
			var current = e.target.dataset.src
			uni.previewImage({
				current: current,
				urls: this.imageList
			})
		},
		getData(){
			uni.request({
			    url: this.url,
				method: 'GET',
			    success: (res) => {
					if(res.data.status===200){
						this.teacher = res.data.data[0]
						console.log(this.teacher)
					}
			    },
				err: (res) => {
					console.log(res)
				}
			});
		},
		back(){
			uni.navigateBack()
		},
		more(){
			uni.showToast({
			    title: '点击更多',
				icon: 'none',
				position: 'bottom',
			    duration: 600
			});
		}
	},
	onPageScroll(option) {
		if(option.scrollTop > 5){
			this.backgroundColor = 'rgba(0,0,0,0.5)'
			this.title = '小z老师'
		}
		else{
			this.backgroundColor = 'transparent'
			this.title = ''
		}
	}
}
